footer {
    margin-top: 10vh;
    margin-bottom: 30px;
    position: absolute;
    vertical-align: bottom;
    width: 100%;
}

.footer-title {
    color: #4c2a74;
    text-transform: uppercase;
    letter-spacing: 1.53px;
    line-height: 1em;
    font-family: Lato;
    font-weight: 500;
    font-style: normal;
    font-size: 30px;
    text-align: center;
    margin-bottom: 30px;
}

.cards {
    text-align: center;
}

.contact {
    text-align: center;
    text-transform: uppercase;
    font-family: Lato;
    font-style: normal;
    letter-spacing: 1.53px;
    line-height: 1em;
    margin-top: 10px;
}

.phone {
    margin-right: 20px;
}

.toolbar {
  top: 0;
  left: 0;
  width: 100%;
  height: 200px;
  margin-bottom: 20px;
}

.toolbar-logo {
  height: 120px;
  width: 120px;
  margin-left: 2rem;
}

.toolbar__navigation {
  display: flex;
  height: 100%;
  align-items: center;
}

.toolbar_navigation-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.toolbar_navigation-items li {
  padding: 0 0.5rem;
}

.toolbar_navigation-items .nav-link {
  color: #008ab7;
  text-decoration: none;
  letter-spacing: 1.53px;
  text-transform: uppercase;
  line-height: 1em;
  font-family: Lato;
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  margin-left: 20px;
}

.toolbar_navigation-items #nav-link-at {
  margin-right: 2rem;
}

.toolbar_navigation-items .nav-link:hover,
.toolbar_navigation-items .nav-link:active {
  color: #ffc432;
}

.spacer {
  flex: 1 1;
}

@media (max-width: 768px) {
  .toolbar_navigation-items {
    display: none;
  }
  .toolbar-logo {
    display: none;
  }
  .toolbar {
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    background-color: #dff7ff;
  }
}

@media (min-width: 769px) {
  .toolbar__toggle-button {
    display: none;
    margin: 25px;
  }
  .toolbar__logo {
    margin-left: 0;
  }
}
.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 25px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
    margin: 20px;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button__line {
    width: 30px;
    height: 2.5px;
    background: #008ab7;
}
.side-drawer {
    height: 100%;
    background: white;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 350px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  }
  
  .side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  
  .side-drawer ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .side-drawer li {
    margin: 0.5rem 0;
  }
  
  .side-drawer .nav-link {
    color: #008ab7;
    text-decoration: none;
    letter-spacing: 1.53px;
    text-transform: uppercase;
    line-height: 4em;
    font-family: Lato;
    font-weight: 700;
    font-style: normal;
    font-size: 15px;
    margin-left: 20px;
  }
  
  .side-drawer .nav-link:hover,
  .side-drawer .nav-link:active {
    color: #ffc432;
  }
  
  @media (min-width: 769px) {
      .side-drawer {
          display: none;
      }
  }
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0px;
}

.alert {
    background-color: #ff4545;
    margin: 50px;
}

form {
    background-color: #dbd5d5;
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color: #00acb0;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #008183;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}

